.fl_chek.color {
	padding-left: 49px !important;
	position: relative;
}
.fl_chek.color:after {
	content: "";
	position: absolute;
	left: 24px;
	top: 3px;
	width: 20px;
	height: 20px;
	border: 1px solid #cccccc;
	border-radius: 50%;
	z-index: 1;
}

.fl_chek[data-color="Красный"]:after {
	background: #ff0000;
}
.fl_chek[data-color="Синий"]:after {
	background: #1100cf;
}
.fl_chek[data-color="Белый"]:after {
	background: #ffffff;
}
.fl_chek[data-color="Бежевый"]:after {
	background: #f3cb7e;
}
.fl_chek[data-color="Черный"]:after {
